<template>
<div id="donate">
  <div class="content card">
    <img :src="url" alt="微信支付">
    <p class="wechat"><span class="icon iconfont iconweixin"></span>微信扫码</p>
    
  </div>
  <div class="list card">
    <div class="header">
      <span class="icon iconfont iconleft yellow"></span>
      <span class="header_name">捐助榜</span>
      <span class="icon iconfont iconright yellow"></span>
    </div>
    <div class="body">

      <div class="loading" v-if="loading">
        <span class="icon iconfont iconloading_filled"></span>
      </div>
      <div class="rows" v-if="donates.length>0">
        <div class="row row_header">
          <span class="name">昵称</span>
          <span class="money">捐助金额</span>
          <span class="date">日期</span>
        </div>
        <div class="row" v-for="donate in donates" :key="donate.id">
          <span class="name" v-text="donate.name"></span>
          <span class="money" v-text="'￥'+donate.money"></span>
          <span class="date" v-text="donate.date"></span>
        </div>
      </div>

    </div>
  </div>
</div>
</template>

<script>

export default {
    name: "Donate",
    components: {

    },
    mounted(){
      this.request();
    },
    watch: {

    },
    computed: {

    },
    data() {
      return {
        url:require('@/assets/img/donate.jpg'),
        donates:[],
        loading: true,
      };
    },
    methods: {
      request: function(index) {
        console.log('请求中...');
        let that = this;
        that.loading = true;
        this.$axios({
            method:'get',
            url:'./donates.json'
        }).then((response) =>{
          console.log('返回数据：'+response) 
          that.donates = response.data;
          that.loading = false;
          console.log('请求成功！');
          }).catch((error) => {
          console.log('请求出错了'+error);
          that.loading = false;
        });
      },
    }
};
</script>

<style lang="less" scoped>
#donate {
  border: 1px solid transparent;

  .content {
    text-align: center;
    
    img {
      width: 12rem;
      height: 12rem;
    }
    .wechat {
      font-size: 1rem;
      color: @c2;
      .icon {
        color: @c_green;
        font-size: 1rem;
        margin-right: 0.5rem;
      }
    }
  }

  .list {
    .header {
      text-align: center;
      height: 1.2rem;
      line-height: 1.2rem;
      font-size: 1.2rem;
      font-weight: bold;
      color: @c1;
    }

    .body {
      .loading {
        margin:2rem 0;
        text-align: center;
        font-size: 1rem;
        font-weight: bold;
        color: @c2;
        .icon {
          display: inline-block;
          width: 1rem;
          height: 1rem;
          line-height: 1rem;
          font-size: 1rem;
          text-align: center;
          animation: rotate linear 1s infinite;
          -moz-animation: rotate linear 1s infinite;	/* Firefox */
          -webkit-animation: rotate linear 1s infinite;	/* Safari 和 Chrome */
          -o-animation: rotate linear 1s infinite;
        }
      }
      .rows {
        max-width: 30rem;
        margin: 0 auto;
        .row {
          margin: 1rem;
          display: flex;
          color: @c1;
          
          .name { 
            flex: 2;
            overflow: hidden;
            text-align: center;
          }
          .money {
            flex: 1;
            text-align: center;
            color: @c_yellow;
            overflow: hidden;
          }
          .date {
            flex: 2;
            overflow: hidden;
            text-align: center;
          }
        }
        .row_header {
          font-weight: bold;
          margin-top: 2rem;
          color: @c2;
          .money {
            color: @c2;
          }
        }
      }
    }
  }
}
</style>