<template>
    <div class="record_list">
        
        <div class="header" @click="isFold=!isFold">
            <span class="border" :class="id=='news'?'news':(id=='olds'?'olds':(id=='seas'?'seas':(id=='s99s'?'s99s':'')))"></span>
            <h3 class="title" v-text="trackLabel"></h3>
            <span class="count" v-text="tracks.length"></span>
            <span :class="'hide'" :id="id">.</span>
            <span class="blank"></span>
            <span class="icon iconfont iconxiala" :class="isFold?'fold':'unfold'"></span>
        </div>
        <!-- <div class="line"></div> -->
        <div class="body" :style="{display:isFold?'none':'block'}">

            <div class="table">

                <div class="track_with_records" 
                    @click="recordDialog=true"
                    v-for="track in tracks" 
                    :key="track.id">


                    <div class="track cell" :style="{height:(track.records.length*3) +'rem' }">

                        <span class="track_name" v-text="track.name"></span>
                        <div class="white_block" :style="{height:(track.records.length*3) +'rem' }">
                            <p class="top icon iconfont iconzuoshangjiao" v-if="track.records.length>1"></p>
                            <p class="bottom icon iconfont iconzuoxiajiao" v-if="track.records.length>1"></p>
                        </div>
                        
                    </div>


                    <div class="records" :style="{flex:isPet?23:19}">
                        <div class="record" 
                            v-for="record in track.records"
                            :key="record.id">
                            
                            <div class="cell time">
                                <a v-text="record.time" :href="record.url" target="_blank" :title="record.url?'点击将跳转至B站':'暂无视频链接'" :class="record.url?'under_line':''"></a>
                            </div>

                            <div class="cell player">
                                <span v-text="record.player.name"></span>
                                <span class="white_block"></span>
                            </div>

                            <div class="cell car" :class="record.car.ecu?'yellow':''">
                                <span v-text="record.car.name" :class="{'red':record.car.rank=='X'}"></span>
                                <span class="white_block"></span>
                            </div>

                            <div class="cell pet"  v-if="isPet">
                                <span v-text="record.pet.name"></span>
                                <span class="white_block"></span>
                            </div>

                            <div class="cell date">
                                <span class="date" v-text="record.dateCH"></span>
                            </div>
                        </div>
                    </div>
                
                </div>
            </div>

        </div>
        <el-dialog
            :visible.sync="recordDialog"
            width="80%"
            center>
            <span>这里展示这张图国服记录时间线</span>
        </el-dialog>
    </div>
</template>

<script>

export default {
    name: 'RecordList',
    props: {
        tracks: Array,
        trackLabel: String,
        id: String,
        isEcu: Boolean,
        isPet: Boolean
    },
    // mounted(){

    // },
    components: {

    },
    data() {
        return {
            isFold: false,
            recordDialog:false,
        };
    },
    methods: {
        info: function(id) {
            this.$emit('info',id);
        }
    }
}
</script>

<style lang="less" scoped>


.record_list {
    padding: 0;
    //头
    .header {
        cursor: pointer;
        padding: 1rem;
        height: 1rem;
        align-items: center;
        display: flex;
        //border-top: 0.2rem solid @c1;
        //border-bottom: 0.2rem solid @c1;

        .border {
            height: 0.8rem;
            width: 0.2rem;
            border-radius: 0.1rem;
            overflow: hidden;
            background-color: @c_yellow;
        }
        .news {
            background-color: @c_green;
        }
        .olds {
            background-color: @c3;
        }
        .s99s {
            background-color: @c_red;
        }
        .seas {
            background-color: @c_blue;
        }

        
        .title {
            color: @c1;
            font-size: 1rem;
            font-weight: 600;
            margin-left: 0.5rem;
        }
        .count {
            margin-left: 0.5rem;
            font-size: 0.8rem;
            color: @c3;
        }
        
        .iconxiala {
            font-size: 0.8rem;
            color: @c4;
        }
    }

    .body {
        .table {
            .track_with_records {
                display: flex;
                align-items: center;
                padding: 0 1rem;

                &:nth-child(odd) {
                    background-color: @c_bg;

                    .white_block {
                        background-image: linear-gradient(to right, rgba(255,255,255,0), @c_bg) !important;
                    }
                }
                &:hover {
                    background-color: @c_yellow_bg;

                    .white_block {
                        background-image: linear-gradient(to right, rgba(255,255,255,0), @c_yellow_bg) !important;
                    }
                }


                .white_block {
                    display: block;
                    position: absolute;
                    right: 0;
                    top: 0;
                    float: right;

                    width: 2rem;
                    height: 1.2rem;
                    background-image: linear-gradient(to right, rgba(255,255,255,0), rgb(255,255,255));
                }


                .cell {
                    text-align: left;
                    font-size: 1rem;
                    height: 1.2rem;
                    line-height: 1.2rem;
                    overflow: hidden;
                    white-space:nowrap;
                    position: relative;
                    text-align: center;
                }

                .track {
                    flex: 5;
                    text-align: left;
                    height: auto;
                    display: flex;
                    align-items: center;
                    //height: 4.2rem;

                    .white_block {
                        //height: 4.2rem;
                        position: absolute;
                        color: @c4;
                        .top {
                            position: absolute;
                            top: 1rem;
                            right: 0;
                        }
                        .bottom {
                            position: absolute;
                            bottom: 1rem;
                            right: 0;
                        }
                    }
                        
                    
                }
                .records {
                    flex: 23;
                    .record {
                        display: flex;
                        text-align: center;
                        cursor: pointer;
                        padding: 1rem 0;
                        //margin: 0.5rem;

                        .hide {
                            width: 0.5rem;
                            height: 1rem;
                        }
                        
                        .time {
                            //background-color: greenyellow;
                            cursor:default;
                            width: 4rem;
                            color: @c_yellow;
                            a {
                                color: @c_yellow;
                            }
                            .under_line {
                                cursor: pointer;
                                color: @c_bili;
                                text-decoration: underline;
                            }
                        }
        
                        .player {
                            //background-color: greenyellow;
                            flex: 6;
                        }
                        
        
                        .car {
                            //background-color: greenyellow;
                            flex: 4.5;
                        }
                        .pet {
                            //background-color: pink;
                            flex: 4;
                        }
                        .date {
                            //background-color: greenyellow;
                            text-align: right;
                            width: 4.5rem;
                            font-size: 0.8rem;
                            color: @c3;
                        }
                    }
                }
            }
        }
    }
}

</style>
