<template>
  <div id="home">
    <!-- 主要信息 -->
    <div id="main">
      <!-- banner菜单 -->
      <div id="banner" class="card">
        <div class="menus">
          <div class="btn" @click="carDrawer=!carDrawer">
              <div class="car pic">
                <span class="icon iconfont iconcar1"></span>
                <span class="icon iconfont iconcar2"></span>
              </div>
              <div class="select">
                <span class="car_rank label" v-text="getCarName">A车</span>
                <span class="icon iconfont iconxiala" :class="carDrawer?'rotate180':''"></span>
              </div>
          </div>
          <div class="btn" @click="clickPet()" :class="isPet?'':'btn_grey'" v-show="isShowPet">
              <div class="pet pic">
                <span class="icon iconfont iconpet1"></span>
                <span class="icon iconfont iconpet2"></span>
              </div>
              <div class="select">
                <span class="is_pet label">宠物</span>
                <span class="icon iconfont" :class="isPet?'iconjianchacheck34':'iconjinzhiforbidden1'"></span>
              </div>
          </div>
          <div class="btn" @click="clickEcu()" :class="isEcu?'':'btn_grey'" v-show="isShowEcu">
              <div class="ecu pic">
                <span class="icon iconfont iconecu1"></span>
                <span class="icon iconfont iconecu2"></span>
                <span class="icon iconfont iconecu3"></span>
                <span class="icon iconfont iconecu4"></span>
                <span class="icon iconfont iconecu5"></span>
              </div>
              <div class="select">
                <span class="is_ecu label">ECU</span>
                <span class="icon iconfont" :class="isEcu?'iconjianchacheck34':'iconjinzhiforbidden1'"></span>
              </div>
          </div>
        </div>
      </div>

      <router-view></router-view>

    </div>

    

    <!-- 侧边栏 -->
    <div id="side">
      
      <!-- posts -->
      <div id="post" class="card">
        <p class="title">说明</p>
        <div class="content">
          
          <section>
            <p>1.赛车在正式服上线之前所有产生的记录不计入国服记录，同毫秒计为并列国服记录；</p>
            <p>2.有视频的链接的记录标记<u class="bili">超链接</u>，否则用黑色标注；</p>
            <p>3.使用ECU的赛车<span class="yellow">黄色</span>标注，否则用黑色标注；</p>
            <p>4.当悬浮车打破普通车记录时，并列排名，悬浮记录<span class="red">红色</span>标注（区分于以鲜红色标注的普通车记录）；</p>
          </section>
          
          <section>
            <div class="line"></div>
            <p>新赛道上线前三天为新赛道保护期，进入保护期的赛道用黄色底色标注，期限内当天投稿最快记录写入榜单但不计入榜单统计，保护期结束后计入榜单。</p>
          </section>
          
          <section>
            <div class="line"></div>
            <p>赛车/宠物若使用皮肤，在记录详细信息中直接表示为皮肤名。</p>
          </section>

          <section>
            <div class="line"></div>
            <p>记录参考价值：联赛图＞非联赛图，普通车＞悬浮车。</p>
          </section>
          <section>
            <div class="line"></div>
            <p>1.车手Top榜中加粗标注的为无车队；</p>
            <p>2.Top榜中并列排名按首字母排列，并列排名不分先后（特殊情况除外）。</p>
          </section>
        </div>
        <span class="post_btn btn1">投稿</span>
      </div>

      <!-- tips -->
      <div id="notice" class="card">
        <p>兴趣使然建立的网站</p>
        <p>谢谢支持~</p>
        <router-link to="/donate" class="donate">
          <span>捐助本站</span>
        </router-link>
      </div>

      <!-- 联系方式 -->
      <div id="contact" class="card">
        <!-- <span class="title">联系我们</span> -->
        <p @click="joinQQGroup()"><span class="qq icon iconfont icontubiao215"></span>加入交流群</p>
        <p @click="QQFeedback()"><span class="qq icon iconfont icontubiao215"></span>网站建议与反馈</p>
        <textarea id="qq_group" style="position: absolute;top: 0;left: 0;opacity: 0;z-index: -10;">1515445451</textarea>
        <textarea id="qq_feedback" style="position: absolute;top: 0;left: 0;opacity: 0;z-index: -10;">1491822925</textarea>
      </div>

  </div>



  <!-- 赛车弹框 -->
  <el-drawer
      :visible.sync="carDrawer"
      direction="btt"
      size="auto"
      :with-header="false"
      custom-class="carDrawer"
      >

      <div class="center">
        <div class="header">
            <span class="blank_btn"></span>
            <span class="title">选择榜单类型</span>
            <span class="icon iconfont iconx1" @click="carDrawer = false"></span>
        </div>
        <div class="line"></div>

        <div class="body">
          <p class="second_title">赛车类型</p>
          <div class="group">
              <span class="label" @click="clickCarRank('a')" :class="{'active':carRank=='a'}">A车</span>
              <span class="label" @click="clickCarRank('x')" :class="{'active':carRank=='x'}">悬浮车</span>
              <span class="label" @click="clickCarRank('nodrift')" :class="{'active':carRank=='nodrift'}">A车(抓地平跑)</span>
          </div>
          <div class="group">
            <span class="label" @click="clickCarRank('b')" :class="{'active':carRank=='b'}">B车</span>
            <span class="label" @click="clickCarRank('c')" :class="{'active':carRank=='c'}">C车</span>
            <span class="label" @click="clickCarRank('d')" :class="{'active':carRank=='d'}">D车</span>
          </div>

          <div class="group">
            <span class="label" @click="clickCarRank('l')" :class="{'active':carRank=='l'}">滑板</span>
            <span class="empty"></span>
            <span class="empty"></span>
          </div>

          <p class="second_title">其他榜单</p>

          <div class="group">
            <span class="label" @click="clickCarRank('s-cup')" :class="{'active':carRank=='s-cup'}">S联赛</span>
            <span class="label" @click="clickCarRank('asia-cup')" :class="{'active':carRank=='asia-cup'}">亚洲杯</span>
            <span class="label" @click="clickCarRank('dance')" :class="{'active':carRank=='dance'}">舞蹈</span>
          </div>
        </div>

      </div>
  </el-drawer>

</div>

</template>

<script>
import List from "@/views/List";


export default {
    name: "home",
    components: {
      List
    },
    mounted() {
      //this.initStatus(listType);
    },
    watch:{
      carRank: function(val){
        if(val!='s-cup' && val!='asia-cup' && val!='dance') {
          this.isShowEcu = (val != 'l');
          this.isShowPet = true;
        }else {
          this.isShowEcu = false;
          this.isShowPet = false;
        }
      },
    },
    computed:{
       listType: function() {
        if(this.carRank!='s-cup' && this.carRank!='asia-cup' && this.carRank!='dance'){
          return this.carRank + (this.isPet?'-pet':'') + (this.isEcu?'-ecu':'');
        }else {
          return this.carRank;
        }
      },
      getCarName: function(){
        switch(this.carRank){
          case 'a': return 'A车';break;
          case 'b': return 'B车';break;
          case 'c': return 'C车';break;
          case 'd': return 'D车';break;
          case 'x': return '悬浮车';break;
          case 'l': return '滑板';break;
          case 'nodrift': return '抓地';break;
          case 's-cup': return 'S联赛';break;
          case 'asia-cup': return '亚洲杯';break;
          case 'dance': return '舞蹈';break;
          default: return '选择';break;
        }
      }
    },

    data(){
      return {
        carRank:'',
        isPet:true,
        isEcu:true,
        isShowPet:true,
        isShowEcu:true,
        carDrawer:false,
      }
    },

    methods:{

      pushUrl: function() {
        if(this.listType!=this.$route.params.type){
          this.$router.push({ name: 'list', params: { type: this.listType }});
          console.log('导航到->'+this.listType);
        }
      },

      clickPet: function(){
        this.isPet = !this.isPet;
        this.pushUrl();
      },

      clickEcu: function(){
        this.isEcu = !this.isEcu;
        this.pushUrl();
      },

      clickCarRank: function(carRank){
        if(this.carRank!= carRank){
          this.carRank = carRank;
          if(this.carRank!='s-cup' && this.carRank!='asia-cup' && this.carRank!='dance'){
            switch(this.carRank){
              case 'a': this.isPet = true;this.isEcu = true;break;
              case 'b': this.isPet = true;this.isEcu = false;break;
              case 'c': this.isPet = true;this.isEcu = false;break;
              case 'd': this.isPet = false;this.isEcu = false;break;
              case 'l': this.isPet = true;this.isEcu = false;break;
              case 'd': this.isPet = false;this.isEcu = false;break;
              case 'x': this.isPet = true;this.isEcu = false;break;
              case 'nodrift': this.isPet = false;this.isEcu = false;break;
              default: break;
            }
          }
          this.pushUrl();
        }
        this.carDrawer=false;
      },

      joinQQGroup: function() {
        let input = document.getElementById("qq_group");
        input.select(); // 选中文本
        document.execCommand("copy"); // 执行浏览器复制命令
        alert("已复制群号（"+input.value+"），去QQ粘贴搜索吧~");
      },

      QQFeedback: function() {
        let input = document.getElementById("qq_feedback");
        input.select(); // 选中文本
        document.execCommand("copy"); // 执行浏览器复制命令
        alert("已复制QQ（"+input.value+"），去QQ粘贴搜索吧~");
      },
    }

};
</script>

<style lang="less" scoped>
/deep/ :focus {
  outline: 0;
}
#home {
  border: 1px solid transparent;

  //主要信息
  #main {

    // banner菜单
    #banner {
      .menus {
        display: flex;
      
        .btn:nth-child(2) {
          margin: 0 1rem;
        }
        
        // 普通按钮
        .btn{
          flex: 1;
          cursor: pointer;
          background: @c_yellow_bg;
          border-radius: 1rem;
          display: flex;
          align-items: center;
          flex-wrap: wrap;
          justify-content: center;
    
          &:hover {
            .car {
              .iconcar1 {
                
              }
              .iconcar2 {
                
              } 
            }
          }
    
          // 赛道标
          .pic {
            position: relative;
            display: inline-block;
            height: 4rem;
            width: 4rem;
            margin: 0.2rem;
            span {
              position: absolute;
              top: 0;
              left: 0;
              color: @c_yellow;
              height: 4rem;
              line-height: 4rem;
              font-size: 4rem;
            }
          }
  
          .car {
            .iconcar1 {
              color: @c_yellow;
              background-image: -webkit-linear-gradient(bottom,@c_yellow,white); 
              background-clip: text; 
              -webkit-background-clip: text; 
              -webkit-text-fill-color: transparent; 
            }
            .iconcar2 {
              color: @c1;
            } 
          }
    
          .pet {
            .iconpet1 {
              color: @c_yellow;
              background-image: -webkit-linear-gradient(bottom,@c_yellow,#fff8ee); 
              background-clip: text; 
              -webkit-background-clip: text; 
              -webkit-text-fill-color: transparent; 
            }
            .iconpet2 {
              color: @c1;
            }
          }
    
          .ecu {
            .iconecu1 {
              color: @c_yellow;
              background-image: -webkit-linear-gradient(left,white,@c_yellow); 
              background-clip: text; 
              -webkit-background-clip: text; 
              -webkit-text-fill-color: transparent; 
              text-shadow: 0 0 0.1rem @c_yellow;
              opacity: 0.5;
            }
            .iconecu2 {
              color: @c_yellow;
              background-image: -webkit-linear-gradient(top,@c_yellow,#fff8ee,white); 
              background-clip: text; 
              -webkit-background-clip: text; 
              -webkit-text-fill-color: transparent; 
              text-shadow: 0 0 0.5rem @c_yellow;
            }
            .iconecu3 {
              color: @c_yellow;
              background-image: -webkit-linear-gradient(top,#fff8ee,white); 
              background-clip: text; 
              -webkit-background-clip: text; 
              -webkit-text-fill-color: transparent; 
              
            }
            .iconecu4 {
              color: @c1;
              background-image: -webkit-linear-gradient(left,#303133,#fff8ee); 
              background-clip: text; 
              -webkit-background-clip: text; 
              -webkit-text-fill-color: transparent; 
            }
            .iconecu5 {
              color: white;
              background-image: -webkit-linear-gradient(left,#fff8ee,white); 
              background-clip: text; 
              -webkit-background-clip: text; 
              -webkit-text-fill-color: transparent;
              text-shadow: 0 0 0.2rem white;
            }
          }
    
          .select {
            margin: 0.5rem 0.2rem;
            width: 6rem;
            display: flex;
            align-items: center;
            justify-content: center;
            .label {
              font-size: 1.2rem;
              color: @c1;
              margin-left: 0.6rem;
              font-weight: bold;
            }
            .icon {
              width: 1.4rem;
              font-size: 1.4rem;
              text-align: center;
              color: @c_yellow;
              margin-left: 0.3rem;
            }
            .iconxiala {
              font-size: 1rem;
            }
    
          }
        }
    
        // 禁用后的按钮
        .btn_grey {
          background: @c_bg;
    
          .pet {
            .iconpet1 {
              color: @c3;
              background-image: -webkit-linear-gradient(bottom,@c3,#ffffff); 
              background-clip: text; 
              -webkit-background-clip: text; 
              -webkit-text-fill-color: transparent; 
            }
            .iconpet2 {
              color: @c1;
            }
          }
    
          .ecu {
            .iconecu1 {
              color: @c3;
              background-image: -webkit-linear-gradient(left,white,@c3);
              text-shadow: 0 0 0.1rem @c3;
            }
            .iconecu2 {
              color: @c3;
              background-image: -webkit-linear-gradient(top,@c3,#fff8ee,white);
              text-shadow: 0 0 0.5rem @c3;
            }
            .iconecu3 {
              color: @c3;
              background-image: -webkit-linear-gradient(top,#fff8ee,white);
              
            }
            .iconecu4 {
              color: @c1;
              background-image: -webkit-linear-gradient(left,#303133,#fff8ee);
            }
            .iconecu5 {
              color: white;
              background-image: -webkit-linear-gradient(left,#fff8ee,white);
            }
          }
    
          .select {
            .label {
              color: @c2;
            }
            .icon {
              color: @c3;
            }
          }
        }
      }
    }

    
    
  }
 

  // 侧边栏
  #side {

    // 投稿
    #post {
      .title {
        font-weight: bold;
        color: @c1;
        text-align: center;
        font-size: 1.1rem;
      }
      .content {
        margin-top: 1rem;
        color: @c2;
        section {
          margin-bottom: 1rem;
          p{
            margin-bottom: 0.5rem;
            line-height: 1.5rem;
          }
        }
        .line {
          height: 0.1rem;
          border-radius: 0.05rem;
          background-color: @c_body_bg;
          margin-bottom: 1rem;
        }
      }
    }
    
    // 注释
    #notice {
      font-size: 1rem;
  
      p {
        color: @c2;
        margin-bottom: 0.5rem;
      }
  
      .donate {
        color: @c_yellow;
        display: inline-block;
        height: 2rem;
        line-height: 2rem;
        padding: 0 0.5rem;
        border-radius: 0.5rem;
        background-color: @c_yellow_bg;
      }
    }
  
    //联系我们
    #contact {
      font-size: 1rem;
      color: @c2;

      p {
        cursor: pointer;
        margin-top: 0.5rem;

        &:hover {
          color: @c1;
        }
        &:first-child {
          margin-top: 0;
        }
        .qq {
          margin-right: 0.1rem;
          color: @c_blue;
          font-size: 1rem;
        }
        a {
          color: @c2;
        }
      }
    }
  }



 
  .carDrawer {
    padding-bottom: 3rem;
    .header {
        margin: 0 1rem;
        height: 3rem;
        color: @c2;
        display: flex;
        align-items: center;
        .iconx1 {
            width: 2rem;
            height: 2rem;
            color: @c3;
            font-size: 1.5rem;
            line-height: 2rem;
            float: left;
            cursor: pointer;
            text-align: center;

            &:hover {
                color: @c1;
            }
        }
        .title {
            flex: 1;
            font-size: 1.2rem;
            font-weight: bold;
            text-align: center;
        }
        .blank_btn {
            width: 2rem;
        }
    }
    .line {
      height: 0.2rem;
      border-radius: 0.1rem;
      background-color: @c_bg;
      margin: 0 1rem;
    }
    .body {
      margin: 0 0.5rem;
      padding-bottom: 3rem;
      .second_title {
        font-size: 1.1rem;
        color: @c3;
        font-weight: bold;
        margin: 1rem;
      }
      .group {
        display: flex;
        .label {
          flex: 1;
          cursor: pointer;
          margin: 0.5rem;
          text-align: center;
          height: 3rem;
          line-height: 3rem;
          font-size: 1rem;
          font-weight: bold;
          border-radius: 0.5rem;
          color:  @c2;
          background-color: @c_bg;
  
          &:hover {
            background-color: @c_yellow_bg;
          }
        }
        .active {
          background-color: @c_yellow_bg;
          color: @c_yellow;
        }
        .empty {
          margin: 0.5rem;
          flex: 1;
        }

      }
    }
  }
}
</style>