    methods: {
        // 初始化判断ECU和宠物状态
        initStatus: function(listType) {
            let parms = listType.split('-');
            let length = parms.length;
            if(length==1) {
            this.$parent.carRank = parms[0];
            this.$parent.isPet = false;
            this.$parent.isEcu = false;
            }else if(length==2){
            let second = parms[1];
            if(second!='cup') {
                this.$parent.carRank = parms[0];
                this.$parent.isPet = (second=='pet');
                this.$parent.isEcu = (second=='ecu');
            }else {
                this.$parent.carRank = listType;
                if(parms[0] == 'asia') {
                this.$parent.isPet = false;
                this.$parent.isEcu = false;
                }else if(parms[0] == 's') {
                this.$parent.isPet = false;
                this.$parent.isEcu = true;
                }
            }
            }else if (length==3) {
            this.$parent.carRank = parms[0];
            let second = parms[1];
            let third = parms[2];
            this.$parent.isPet = (second=='pet' || third=='pet');
            this.$parent.isEcu = (second=='ecu' || third == 'ecu');
            }
            console.log('initStatus'+this.carRank);
        },

        // 初始化列表
        initList: function(listType) {
            console.log('initList()->'+listType);
            this.listOnShow = 'default';

            // 清空存在在列表
            this.tracks = [];
            this.tracksDefault = [];

            // 判断是否为空
            if(listType) {
                //判断是否存在榜单
                let index = -1;
                this.allList.find(function(list,i){
                    if(list.label==listType) {
                    index = i;
                    }
            　　});
                if(index>=0) { 
                    // 存在初始化数据
                    this.notFound = false;
                    let list = this.allList[index];
                    console.log(list);
                    // 初始化列表
                    if(list.tracksDefault.length>0) {
                    console.log(list.label+'的数据已经存在...');
                    this.tracksDefault = list.tracksDefault;
                    this.tracksNew= list.tracksNew;
                    }else {
                    //请求数据
                    this.request(index); 
                    }

                    this.initStatus(listType);

                }else {
                    this.notFound = true;
                }
            }else {
                this.$router.push('/home/a-pet-ecu');
            }
        },

        request: function(index) {
            console.log('请求中...');
            let that = this;
            that.loading = true;

            this.$axios({
                method:'get',
                url:'./records.json'
            }).then((response) =>{
            console.log('返回数据：'+response) 
            that.allList[index].tracksDefault = response.data;
            that.tracksDefault = that.allList[index].tracksDefault;
            //that.initTracksWithRecordsDefault(index);
            //that.initRecordsNew();
            that.loading = false;
            console.log('请求成功！');
            }).catch((error) => {
            console.log('请求出错了'+error);
            that.loading = false;
            });

        },

