                url:'./records.json'
            }).then((response) =>{
                console.log(response)       //请求成功返回的数据
                that.tracks = response.data;
                that.initTracksWithRecordsDefault();
                //that.initRecordsNew();
                that.loading = false;
            }).catch((error) => {
                console.log(error)       //请求失败返回的数据
            });
        },

        // 初始化默认数据
        initTracksWithRecordsDefault: function(index){
            this.tracksDefault = [];
            let news = {label:'新赛道',id:'news',tracks:[]};
            let s7s = {label:'7星赛道',id:'s7s',tracks:[]};
            let s6s = {label:'6星赛道',id:'s6s',tracks:[]};
            let s5s = {label:'5星赛道',id:'s5s',tracks:[]};
            let s4s = {label:'4星赛道',id:'s4s',tracks:[]};
            let s3s = {label:'3星赛道',id:'s3s',tracks:[]};
            let s2s = {label:'2星赛道',id:'s2s',tracks:[]};
            let s1s = {label:'1星赛道',id:'s1s',tracks:[]};
            let s99s = {label:'拉力赛道',id:'s99s',tracks:[]};
            let seas = {label:'航海赛道',id:'seas',tracks:[]};
            let olds = {label:'怀旧赛道',id:'olds',tracks:[]};

            for (var t of this.tracks) {
                if(t.new) {
                    news.push(t);
                }else if(t.old) {
                    olds.push(t);
                }else if(t.sea) {
                    seas.push(t);
                }else {
                    switch(t.stars) {
                        case 4: s4s.tracks.push(t);break;
                        case 3: s3s.tracks.push(t);break;
                        case 2: s2s.tracks.push(t);break;
                        case 1: s1s.tracks.push(t);break;
                        case 6: s6s.tracks.push(t);break;
                        case 5: s5s.tracks.push(t);break;
                        case 99: s99s.tracks.push(t);break;
                        case 7: s7s.tracks.push(t);break;
                        default: break;
                    }
                }
                //console.log(r);
            }

            if(news.tracks.length > 0) this.tracksDefault.push(news);
            if(s7s.tracks.length > 0) this.tracksDefault.push(s7s);
            if(s6s.tracks.length > 0) this.tracksDefault.push(s6s);
            if(s5s.tracks.length > 0) this.tracksDefault.push(s5s);
            if(s4s.tracks.length > 0) this.tracksDefault.push(s4s);
            if(s3s.tracks.length > 0) this.tracksDefault.push(s3s);
            if(s2s.tracks.length > 0) this.tracksDefault.push(s2s);
            if(s1s.tracks.length > 0) this.tracksDefault.push(s1s);
            if(s99s.tracks.length > 0) this.tracksDefault.push(s99s);
            if(seas.tracks.length > 0) this.tracksDefault.push(seas);
            if(olds.tracks.length > 0) this.tracksDefault.push(olds);


            this.allList[index].tracksDefault = this.tracksDefault;

        },

        // 初始化最新数据
        initRecordsNew: function(){
            // let newArr = [];
            // this.tracks.forEach((item,i)=>{
            //     let index = -1;
            //     let isExists = newArr.some((newItem,j)=>{
            //         if(item.date==newItem.label){
            //             index = j;
            //             return true;
            //         }
            //     })
            //     if(!isExists){
            //         let newId = 'id'+item.date.replace(/-/g,'');
            //         newArr.push({
